import React from 'react';
import { Link } from "react-router-dom";
import '../styles/section.css';

const Section = (props) => {
	let chapters = props.chapters.map((chapter, i) => {     
		return (
			<Link key={i} to={"/courses/"+props.section+"/"+chapter.link} className="chapter clear">
				<div className="name">{chapter.name}</div>
				<div className="arrow"></div>
			</Link>
		)                 
	})

    return(
        <div className="section">
        	<div className="top shadow" style={{backgroundImage:'linear-gradient(rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.8)), url(/psm-i.jpeg)'}}>
				<div>
					<h1>{props.name}</h1>
					<p>{props.section}</p>
				</div>
			</div>
    		<div className="chapters shadow">
    			{chapters}
    		</div>
    	</div>
    )
}

export default Section;